.grid-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  background-color: white;
  touch-action: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.grid-container.dark {
  background-color: #111827;
}

.dark .grid-container {
  background-color: #111827;
}

.grid-svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.grid-line {
  stroke: #d1d5db;
  stroke-width: 0.5px;
}

.grid-container.dark .grid-line {
  stroke: #4b5563;
}

.dark .grid-line {
  stroke: #4b5563;
}

.grid-cell {
  cursor: pointer;
}

.grid-cell.selected {
  fill: rgba(59, 130, 246, 0.1);
  stroke: rgb(59, 130, 246);
  stroke-width: 2px;
}

.grid-cell.unselected {
  fill: transparent;
  stroke: transparent;
  stroke-width: 0;
}

.grid-text {
  pointer-events: none;
  user-select: none;
  fill: #111827;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

.grid-container.dark .grid-text {
  fill: #f9fafb;
}

.dark .grid-text {
  fill: #f9fafb;
}

.hidden-input {
  position: absolute;
  left: -9999px;
  opacity: 0;
}