.on-screen-keyboard {
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 12px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  min-width: 320px;
  max-width: 400px;
  user-select: none;
}

@media (max-width: 480px) {
  .on-screen-keyboard {
    min-width: 280px;
    max-width: 90vw;
  }
}

.keyboard-header {
  background: #f3f4f6;
  border-radius: 12px 12px 0 0;
  padding: 12px 16px;
  cursor: move;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #e5e7eb;
  user-select: none;
}

.keyboard-header:active {
  cursor: grabbing;
}

.keyboard-title {
  font-weight: 600;
  color: #374151;
  font-size: 14px;
}

.keyboard-close {
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
  color: #6b7280;
  padding: 4px 8px;
  border-radius: 4px;
  line-height: 1;
}

.keyboard-close:hover {
  background: #e5e7eb;
  color: #374151;
}

.keyboard-body {
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.calculator-section {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.additional-section {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding-top: 8px;
  border-top: 1px solid #e5e7eb;
}

.line-section {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding-top: 8px;
  border-top: 1px solid #e5e7eb;
}

.special-section {
  display: flex;
  gap: 8px;
  padding-top: 8px;
  border-top: 1px solid #e5e7eb;
}

.calculator-row {
  display: flex;
  gap: 8px;
}

.key-group {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.key-group.numbers {
  justify-content: center;
}

.key-group.operators {
  justify-content: center;
}

.key-group.letters {
  justify-content: center;
}

.key-group.symbols {
  justify-content: center;
}

.key-group.special {
  justify-content: center;
}

.calculator-key {
  background: #f9fafb;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  padding: 0;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.15s ease;
  flex: 1;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #374151;
  touch-action: manipulation;
  min-width: 0;
}

.calculator-key.span-2 {
  flex: 2;
}

.calculator-key.number {
  background: #f9fafb;
  border-color: #d1d5db;
  color: #374151;
}

.calculator-key.operator {
  background: #3b82f6;
  border-color: #2563eb;
  color: white;
  font-weight: 700;
}

.calculator-key.clear {
  background: #ef4444;
  border-color: #dc2626;
  color: white;
  font-weight: 700;
}

.calculator-key.symbol {
  background: #f3f4f6;
  border-color: #9ca3af;
  color: #374151;
}

.calculator-key.letter {
  background: #10b981;
  border-color: #059669;
  color: white;
  font-weight: 600;
}

.calculator-key.line {
  background: #8b5cf6;
  border-color: #7c3aed;
  color: white;
  font-weight: 700;
  font-size: 20px;
}

.calculator-key.special {
  background: #6b7280;
  border-color: #4b5563;
  color: white;
  font-weight: 600;
}

/* ホバー効果 */
.calculator-key:hover {
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}

.calculator-key.number:hover {
  background: #f3f4f6;
  border-color: #9ca3af;
}

.calculator-key.operator:hover {
  background: #2563eb;
  border-color: #1d4ed8;
}

.calculator-key.clear:hover {
  background: #dc2626;
  border-color: #b91c1c;
}

.calculator-key.symbol:hover {
  background: #e5e7eb;
  border-color: #6b7280;
}

.calculator-key.letter:hover {
  background: #059669;
  border-color: #047857;
}

.calculator-key.line:hover {
  background: #7c3aed;
  border-color: #6d28d9;
}

.calculator-key.special:hover {
  background: #4b5563;
  border-color: #374151;
}

/* アクティブ効果 */
.calculator-key:active {
  transform: translateY(1px);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

.key-button {
  background: #f9fafb;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  padding: 8px 12px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.15s ease;
  min-width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #374151;
  touch-action: manipulation;
}

@media (max-width: 480px) {
  .calculator-key {
    height: 42px;
    font-size: 16px;
  }
  
  .key-button {
    min-width: 35px;
    height: 35px;
    font-size: 14px;
    padding: 6px 10px;
  }
  
  .keyboard-body {
    padding: 12px;
    gap: 12px;
  }
  
  .calculator-row {
    gap: 6px;
  }
  
  .special-section {
    gap: 6px;
  }
}

.key-button:hover {
  background: #e5e7eb;
  border-color: #9ca3af;
}

.key-button:active {
  background: #d1d5db;
  transform: translateY(1px);
}

.key-group.operators .key-button {
  background: #dbeafe;
  border-color: #93c5fd;
  color: #1e40af;
}

.key-group.operators .key-button:hover {
  background: #bfdbfe;
  border-color: #60a5fa;
}

.key-group.letters .key-button {
  background: #f0fdf4;
  border-color: #bbf7d0;
  color: #166534;
}

.key-group.letters .key-button:hover {
  background: #dcfce7;
  border-color: #86efac;
}

.space-key {
  flex: 1;
  min-width: 120px;
}

.backspace-key {
  background: #fef2f2;
  border-color: #fecaca;
  color: #dc2626;
}

.backspace-key:hover {
  background: #fee2e2;
  border-color: #f87171;
}

.keyboard-toggle-button {
  background: #3b82f6;
  border: none;
  border-radius: 50%;
  width: 56px;
  height: 56px;
  font-size: 24px;
  cursor: pointer;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
  transition: all 0.2s ease;
  z-index: 1001;
}

.keyboard-toggle-button:hover {
  background: #2563eb;
  transform: scale(1.05);
  box-shadow: 0 6px 16px rgba(59, 130, 246, 0.4);
}

.keyboard-toggle-button:active {
  transform: scale(0.95);
}

/* ダークモード対応 */
@media (prefers-color-scheme: dark) {
  .on-screen-keyboard {
    background: #1f2937;
    border-color: #374151;
  }

  .keyboard-header {
    background: #374151;
    border-bottom-color: #4b5563;
  }

  .keyboard-title {
    color: #f9fafb;
  }

  .keyboard-close {
    color: #9ca3af;
  }

  .keyboard-close:hover {
    background: #4b5563;
    color: #f9fafb;
  }

  .additional-section {
    border-top-color: #4b5563;
  }

  .line-section {
    border-top-color: #4b5563;
  }

  .special-section {
    border-top-color: #4b5563;
  }

  .calculator-key.number {
    background: #374151;
    border-color: #4b5563;
    color: #f9fafb;
  }

  .calculator-key.number:hover {
    background: #4b5563;
    border-color: #6b7280;
  }

  .calculator-key.symbol {
    background: #4b5563;
    border-color: #6b7280;
    color: #f9fafb;
  }

  .calculator-key.symbol:hover {
    background: #6b7280;
    border-color: #9ca3af;
  }

  .key-button {
    background: #374151;
    border-color: #4b5563;
    color: #f9fafb;
  }

  .key-button:hover {
    background: #4b5563;
    border-color: #6b7280;
  }

  .key-button:active {
    background: #6b7280;
  }

  .key-group.operators .key-button {
    background: #1e3a8a;
    border-color: #3b82f6;
    color: #93c5fd;
  }

  .key-group.operators .key-button:hover {
    background: #1e40af;
    border-color: #60a5fa;
  }

  .key-group.letters .key-button {
    background: #14532d;
    border-color: #16a34a;
    color: #86efac;
  }

  .key-group.letters .key-button:hover {
    background: #166534;
    border-color: #22c55e;
  }

  .backspace-key {
    background: #7f1d1d;
    border-color: #dc2626;
    color: #fca5a5;
  }

  .backspace-key:hover {
    background: #991b1b;
    border-color: #ef4444;
  }
}